import { pluginName } from './constants';

const { CKEDITOR } = window;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colorMap = CKEDITOR.tools.get_plugin_config(pluginName, editor);


    /* Utilities */
    const utils = {
      cloneElement(source) {
        const clone = new CKEDITOR.dom.element(source.getName());
        Object.entries(source.getAttributes()).forEach(([key, value]) => clone.setAttribute(key, value));
        return clone;
      },

      /** Wrap `node` with clones of every element in `chain` (inner‑to‑outer). */
      wrapWithAncestors(node, chain) {
        return chain.reduceRight((inner, outer) => {
          const wrapper = utils.cloneElement(outer);
          wrapper.append(inner);
          return wrapper;
        }, node);
      },

      /** Split a text node into three parts according to a range. */
      splitTextByRange(textNode, range) {
        const start = textNode.equals(range.startContainer) ? range.startOffset : 0;
        const end = textNode.equals(range.endContainer) ? range.endOffset : textNode.getLength();
        return [textNode.substring(0, start), textNode.substring(start, end), textNode.substring(end)];
      }
    };

    /* Ensure colour <span> wraps the inline tag, not vice‑versa */
    function liftColorSpans(range) {
      const walkerRange = range.clone();
      walkerRange.enlarge(CKEDITOR.ENLARGE_INLINE);

      const walker = new CKEDITOR.dom.walker(walkerRange);
      walker.evaluator = node =>
        node?.type === CKEDITOR.NODE_ELEMENT &&
        node.getName() === 'span' &&
        node.getStyle('color');

      let span;
      while ((span = walker.next())) {
        const parentInline = span.getParent();
        // already correct
        if (!parentInline || parentInline.getName() === 'span') continue; 

        const firstChild = span.getFirst();
        // already cloned previously
        if (firstChild?.type === CKEDITOR.NODE_ELEMENT && firstChild.getName() === parentInline.getName()) {
          continue; 
        }

        const inlineClone = utils.cloneElement(parentInline);
        while (span.getFirst()){
          inlineClone.append(span.getFirst().remove());
        }
        span.append(inlineClone);
      }
    }

    /* Remove only the colour while keeping other inline styles */
    function removeColor(range) {
      // Enlarge so partially‑selected inline spans are fully included on first try
      const enlarged = range.clone();
      enlarged.enlarge(CKEDITOR.ENLARGE_INLINE);

      const textWalker = new CKEDITOR.dom.walker(range.clone());
      textWalker.evaluator = node =>
        node.type === CKEDITOR.NODE_TEXT && node.getAscendant('span', true)?.getStyle('color');

      let textNode;
      while ((textNode = textWalker.next())) {
        const colorSpan = textNode.getAscendant('span', true);
        if (!colorSpan) continue;

        const [before, middle, after] = utils.splitTextByRange(textNode, range);

        // Collect ancestors between textNode and the colour span
        const ancestors = [];
        let ancestor = textNode.getParent();
        while (ancestor && !ancestor.equals(colorSpan)) {
          ancestors.unshift(ancestor);
          ancestor = ancestor.getParent();
        }

        const fragments = [];

        // left (keeps colour)
        if (before) {
          const leftFragment = utils.wrapWithAncestors(new CKEDITOR.dom.text(before), ancestors);
          const leftSpan = utils.cloneElement(colorSpan);
          leftSpan.append(leftFragment);
          fragments.push(leftSpan);
        }

        // middle (loses colour)
        if (middle) {
          const middleFragment = utils.wrapWithAncestors(new CKEDITOR.dom.text(middle), ancestors);
          fragments.push(middleFragment);
        }

        // right (keeps colour)
        if (after) {
          const rightFragment = utils.wrapWithAncestors(new CKEDITOR.dom.text(after), ancestors);
          const rightSpan = utils.cloneElement(colorSpan);
          rightSpan.append(rightFragment);
          fragments.push(rightSpan);
        }

        // Insert new fragments and remove original text node
        fragments.forEach(fragment => colorSpan.insertBeforeMe(fragment));

        textNode.remove();

        if (!colorSpan.getChildCount()){
          colorSpan.remove();
        }
      }
    }

    /* RichCombo UI */
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor'), ...editor.config.contentsCss],
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        for (const [label, value] of Object.entries(colorMap)) {
          this.add(value, label, label);
        }
      },
      onRender() {
        this.setValue('');
      },
      onClick(choice) {
        editor.focus();
        let selection = editor.getSelection();
        if (!selection) return;

        editor.fire('lockSnapshot');

        const initialRanges = selection.getRanges();

        if (choice === 'default') {
          initialRanges.forEach(removeColor);
        } else {
          const colorStyle = new CKEDITOR.style({ element: 'span', styles: { color: choice } });
          editor.applyStyle(colorStyle);

          // CKEditor may split nodes; refresh selection
          selection = editor.getSelection();
          selection?.getRanges().forEach(liftColorSpans);
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});
